.modal {
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  background: rgba(0, 0, 0, 0.7);
  z-index: 1000;
  display: none;
}
.modal.show {
  display: block;
}
.modal .dimmed {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
}
.modal .modal-inner {
  background: #fff;
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  width: calc(100% - 20px);
  min-width: 300px;
  max-width: 450px;
  max-height: 500px;
  border-radius: 10px;
  padding: 20px;
  z-index: 100;
}
.modal .modal-inner .btn-close-modal {
  position: absolute;
  right: 15px;
  top: 15px;
  width: 20px;
  height: 20px;
  /* background: url(images/icon/ico_menu_close.png) center center no-repeat; */
  font-size: 0;
  background-size: contain;
}
.modal .modal-inner .modal-header {
  font-size: var(--font-mainTitle);
  font-weight: 600;
}
.modal .modal-inner .modal-cont {
  padding: 20px 0;
  font-size: var(--font-subTitle);
  line-height: calc(var(--font-subTitle) + 3px);
  display: flex;
  align-items: center;
  min-height: 80px;
}
.modal .button-area {
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 15px;
  margin-top: 20px;
}
.modal .button-area button {
  font-size: var(--font-subTitle) !important;
}